import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from './Affairs.module.css'

type AffairsPropsType = { // need to fix any
    data: Array<AffairType>
    setFilter: (filter: FilterType) => void
    deleteAffairCallback: (_id: number) => void
    filter: string
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => {
        props.setFilter('all')
    }
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter('low')
    }


    const setActiveClass = (filter: FilterType) => (props.filter === filter ? ' ' + s.active : '')


    return (
        <div className={s.affairsWrapper}>

            <div>{mappedAffairs}</div>

            <div className={s.buttons}>
                <button onClick={setAll} className={`${s.button} + ${setActiveClass('all')}`} >All</button>
                <button onClick={setHigh} className={`${s.button} + ${setActiveClass('high')}`}>High</button>
                <button onClick={setMiddle} className={`${s.button} + ${setActiveClass('middle')}`}>Middle</button>
                <button onClick={setLow} className={`${s.button} + ${setActiveClass('low')}`}>Low</button>
            </div>
        </div>
    )
}

export default Affairs
